import React, { useEffect, useState } from "react";
import { ethers } from "ethers";

import Matter from "../artifacts/contracts/Matter.sol/Matter.json";
import AntiMatter from "../artifacts/contracts/AntiMatter.sol/AntiMatter.json";
import contracts from "../contracts";
import { Button, Center, Flex, Spacer, Text, Tooltip } from "@chakra-ui/react";
import Countdown from "../components/Countdown";
import { Underline } from "../components/Underline";
import useEthereumCallback from "../hooks/useEthereumCallback";

async function requestAccount() {
  await window.ethereum.request({ method: "eth_requestAccounts" });
}

const getDate = async () => {
  if (typeof window.ethereum === "undefined") {
    return;
  }

  const provider = new ethers.providers.Web3Provider(window.ethereum);
  const contract = new ethers.Contract(contracts.matter, Matter.abi, provider);
  return await contract.unlockTime();
};

const claimMatter = async () => {
  if (typeof window.ethereum === "undefined") {
    return;
  }

  await requestAccount();
  const provider = new ethers.providers.Web3Provider(window.ethereum);
  const signer = provider.getSigner();
  const contract = new ethers.Contract(contracts.matter, Matter.abi, signer);
  await contract.claim();
};

const claimAntiMatter = async () => {
  if (typeof window.ethereum === "undefined") {
    return;
  }

  await requestAccount();
  const provider = new ethers.providers.Web3Provider(window.ethereum);
  const signer = provider.getSigner();
  const contract = new ethers.Contract(contracts.antimatter, AntiMatter.abi, signer);
  await contract.claim();
};

const antimatterBalanceOf = async () => {
  if (typeof window.ethereum === "undefined") {
    return;
  }

  await requestAccount();
  const provider = new ethers.providers.Web3Provider(window.ethereum);
  const signer = provider.getSigner();
  const contract = new ethers.Contract(contracts.antimatter, AntiMatter.abi, signer);
  const { selectedAddress } = provider.provider;
  const balance = await contract.claimed();
  return { balance, selectedAddress };
};

export default function LaunchTime() {
  const { data: date, loading: loadingDate } = useEthereumCallback(getDate);
  const { data: amBalance, loading: loadingAmBalance } = useEthereumCallback(antimatterBalanceOf);

  const claimMatterButton = (
    <Tooltip label="Claim Matter particles to be used only after the explosion">
      <Button w={40} variant={"outline"} onClick={claimMatter}>
        Claim Matter
      </Button>
    </Tooltip>
  );

  const claimAntimatterButton = (
    <Tooltip label="Claim Antimatter particles to be used only after the explosion">
      <Button w={40} variant={"outline"} onClick={claimAntiMatter}>
        Claim Antimatter
      </Button>
    </Tooltip>
  );

  const loader = <Text>Loading...</Text>;

  const claimedAntimatterMessage = (amount) => (
    <Text>
      Claimed {new Intl.NumberFormat("de-DE").format(Math.ceil(amount / Math.pow(10, 18)))}
    </Text>
  );

  const antimatterContent =
    loadingAmBalance || !amBalance
      ? loader
      : amBalance.balance > 0
      ? claimedAntimatterMessage(amBalance.balance)
      : claimAntimatterButton;

  return (
    <Center>
      <Flex color={"white"} direction={"column"} textAlign={"center"}>
        <Underline fontSize={60} fontWeight={200} border={2} mb={40}>
          The <b>Big Bang</b> is about to occur
        </Underline>

        {loadingDate && <Text>Loading date...</Text>}
        {date && <Countdown deadline={date} />}

        <Flex mt={40}>
          {claimMatterButton}
          <Spacer />
          {antimatterContent}
        </Flex>
      </Flex>
    </Center>
  );
}
