import { useEffect, useState } from "react";

export default function useEthereumCallback(callback) {
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  useEffect(() => {
    callback()
      .then((response) => {
        setData(response);
        setLoading(false);
      })
      .catch((err) => {
        setError(err);
        setLoading(false);
      });
    setLoading(true);
  }, [callback]);

  return { data, loading, error };
}
